<template>
    <section class="min-h-full w-full pb-[5rem] lg:pb-[2rem]">
        <div class="mb-8 flex justify-between items-center">
            <h3 class="title-vista text-2xl font-semibold text-neutral-600">Administrar conductores</h3>
            <button @click="openModal"
                class="py-2 px-3 rounded-xl bg-orange-400 text-white lg:text-sm text-xs hover:bg-orange-500 transition duration-300">
                Crear nuevo
            </button>
        </div>
        <div class="overflow-x-auto shadow-md sm:rounded-lg">
            <TableConductores :listConductores="listConductores" @eliminar-conductor="eliminarConductor"
                @editar-conductor="editarConductor" />
        </div>
        <transition name="modal" mode="out-in">
            <ModalCreate v-show="openModalV" @close-modal="closeModal" @save-datos="saveDatos" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalDelete v-if="confirmDelete" @aceptar-eliminar="aceptarEliminar" @cancelar-eliminar="cancelarEliminar"
                pregunta="¿Estás seguro de eliminar a este conductor?" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalEditar v-if="openModalEdit" @aceptar-editar="aceptarEditar" @cancelar-editar="cancelarEditar"
                :idUsuario="idUsuario" :idConductor="idConductor" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalError v-if="openModalError" @close-modal="closeModalError" title="Advertencia"
                mensaje="Se debe rellenar todos los campos al crear o editar un conductor" />
        </transition>
        <transition name="modal" mode="out-in">
            <ConfirmEdit v-if="openSuccessEdit" title="Logrado" mensaje="Se ha editado correctamente"
                @close-modal="closeSuccessEdit" />
        </transition>
    </section>
</template>
<script>
import { ref, onMounted } from 'vue';
import { listarConductores, crearConductores, eliminarConductores, editarConductores } from '../services/ConductoresService';
import TableConductores from '../components/conductores/TableConductores';
import ModalCreate from '../components/conductores/Modalcreate.vue';
import ModalDelete from '../components/generales/ModalDelete.vue';
import ConfirmEdit from '../components/generales/ConfirmEdit.vue';
import ModalError from '../components/generales/ModalError.vue';
import ModalEditar from '../components/conductores/ModalEditar';
import store from '../store'

export default ({
    name: "AdminConductores",
    components:
    {
        TableConductores,
        ModalCreate,
        ModalDelete,
        ModalEditar,
        ModalError,
        ConfirmEdit
    },
    setup() {
        const openModalV = ref(false);
        const openModalEdit = ref(false)
        const confirmDelete = ref(false);
        const listConductores = ref([]);
        const openModalError = ref(false);
        const openSuccessEdit = ref(false)
        const idConductor = ref('');
        const idUsuario = ref('');
        const placa = ref('');
        const openModal = () => {
            openModalV.value = true;
        };

        const closeModal = () => {
            openModalV.value = false;
        }

        const closeSuccessEdit = () => {
            openSuccessEdit.value = false;
            window.location.reload();
        }

        const eliminarConductor = (datatable) => {
            idConductor.value = datatable.id_conductores
            idUsuario.value = datatable.id_usuario
            placa.value = datatable.placa
            confirmDelete.value = true;
        }

        const aceptarEliminar = () => {
            eliminarConductores(idUsuario.value, idConductor.value, placa.value, store.state.codempresa)
                .then(response => {
                    console.log(response.data)
                    confirmDelete.value = false;
                    idConductor.value = "";
                    idUsuario.value = "";
                    window.location.reload();
                })
                .catch((error) => {
                    console.log(error)
                })
        }

        const cancelarEliminar = () => {
            idConductor.value = "";
            idUsuario.value = "";
            confirmDelete.value = false;
        }

        const cancelarEditar = () => {
            openModalEdit.value = false;
        }
        const editarConductor = (datatable) => {
            idConductor.value = datatable.id_conductores
            idUsuario.value = datatable.id_usuario
            openModalEdit.value = true;
        }

        const aceptarEditar = (dataformulario) => {
            const id_usuario = dataformulario.id_usuario;
            const id_conductor = dataformulario.id_conductor;
            const apellidos = dataformulario.apellidos;
            const dni = dataformulario.dni;
            const placa = dataformulario.placa;
            const licencia = dataformulario.licencia;
            const usuarios = dataformulario.usuarios;
            const contraseña = dataformulario.contraseña;
            if (apellidos == '' || dni == '' || licencia == '' || usuarios == '' || contraseña == '') {
                openModalV.value = false;
                openModalError.value = true;
            }
            else {
            editarConductores(id_usuario, id_conductor, dni, apellidos, licencia, usuarios,contraseña,placa, store.state.codempresa)
                .then(response => {
                    console.log(response.data)
                    openModalEdit.value = false;
                    openSuccessEdit.value = true;
                })
            }
        }

        const saveDatos = (dataformulario) => {
            const apellidos = dataformulario.apellidos;
            const dni = dataformulario.dni;
            const licencia = dataformulario.licencia;
            const placa = dataformulario.placa;
            const usuarios = dataformulario.usuarios;
            const contraseña = dataformulario.contraseña;
            if (apellidos == '' || dni == '' || licencia == '' || usuarios == '' || contraseña == '' || placa == '') {
                openModalV.value = false;
                openModalError.value = true;
            }
            else {
                crearConductores(dni, apellidos, licencia, store.state.codempresa, usuarios, contraseña, placa)
                    .then(response => {
                        console.log(response.data)
                        openModalV.value = false;
                        window.location.reload();
                    })
            }
        }

        const closeModalError = () => {
            openModalError.value = false;
        }
        onMounted(() => {
            listarConductores(store.state.codempresa)
                .then(response => {
                    listConductores.value = response.data.conductores
                })
        })
        return {
            listConductores,
            openModal,
            openModalV,
            openSuccessEdit,
            closeSuccessEdit,
            closeModal,
            saveDatos,
            confirmDelete,
            eliminarConductor,
            aceptarEliminar,
            cancelarEliminar,
            cancelarEditar,
            editarConductor,
            openModalEdit,
            idConductor,
            idUsuario,
            aceptarEditar,
            openModalError,
            closeModalError
        }
    }
})
</script>
<style scoped>
.modal-enter-active,
.modal-leave-active {
    transition: opacity 0.3s;
}

.modal-enter-from,
.modal-leave-to {
    opacity: 0;
}
</style>