<template>
    <div class="fixed inset-0 flex items-center justify-center z-[9999]">
        <div class="absolute inset-0 bg-black opacity-30 pointer-events-none"></div>
        <!-- Container modal -->
        <div class="relative z-10 p-4 bg-white rounded-xl lg:min-w-[350px] lg:max-w-[450px] min-w-[330px] max-w-[350px]">
            <form class="p-2 text-gray-700 w-full h-full text-sm">
                <h3 class="text-center font-bold text-lg">Editar conductor</h3>
                <ItemForm @value-input="saveApellidos" label="Apellidos y nombres" name="apellidos_nombres"
                    id="apellidos_nombres" placeholder="Inserte el nombre del conductor" :value="apellidos" />
                <ItemForm @value-input="saveDni" label="DNI" name="dni" id="dni" placeholder="Inserte el DNI del conductor"
                    :value="dni" />
                <SelectForm name="Asignar a una unidad" :options="ListselectUnidades" v-model="selectedUnidad"
                    @selected="handleUnidadSelected" class="col-span-2 md:col-span-1 lg:col-span-2 text-sm my-2" />
                <ItemForm @value-input="saveLicencia" label="Número de licencia" name="licencia" id="licencia"
                    placeholder="Inserte el número de licencia" :value="licencia" />
                <ItemForm @value-input="saveUsuario" label="Usuario" name="usuario" id="usuario"
                    placeholder="Inserte un usuario para el conductor" :value="usuarios" />
                <PasswordField @update-paswoord="updatepassword" texto="Contraseña" placeholder="Ingrese una contraseña"
                    :valor="contraseña" />
            </form>
            <div class="btns flex gap-2 justify-center items-center pt-2">
                <button @click="closeModal"
                    class="bg-gray-400 hover:bg-gray-500 text-white text-sm p-2 px-8 rounded-xl transition duration-300">Cerrar</button>
                <button @click="saveDatos"
                    class="bg-blue-400 hover:bg-blue-500 text-white text-sm p-2 px-8 rounded-xl transition duration-300">Actualizar</button>
            </div>
        </div>
        <!-- Container modal -->
    </div>
</template>
  
<script>
import { ref, onMounted } from 'vue';
import ItemForm from '../generales/ItemForm.vue';
import SelectForm from '../generales/SelectForm.vue';
import { listarUnidadesLibres } from '@/services/UnidadesService';
import { buscarConductor } from '../../services/ConductoresService';
import PasswordField from '../generales/PasswordField.vue';
import store from '@/store';
import md5 from 'md5';

export default {
    name: 'ModalEditar',
    components: { ItemForm, PasswordField, SelectForm },
    props: {
        idUsuario: String,
        idConductor: String
    },
    emits: ['cancelar-editar', 'aceptar-editar'],
    setup(props, { emit }) {
        const apellidos = ref('');
        const dni = ref('');
        const licencia = ref('');
        const usuarios = ref('');
        const contraseña = ref('');
        const contraseñaNueva = ref('');
        const ListselectUnidades = ref([]);
        const placa = ref('');
        const selectedUnidad = ref('');
        const closeModal = () => {
            emit('cancelar-editar')
        }
        const saveApellidos = (value) => {
            apellidos.value = value;
        }
        const saveDni = (value) => {
            dni.value = value;
        }
        const saveLicencia = (value) => {
            licencia.value = value;
        }
        const saveUsuario = (value) => {
            usuarios.value = value;
        }
        const updatepassword = (value) => {
            contraseñaNueva.value = value;
        }
        const handleUnidadSelected = (value) => {
            selectedUnidad.value = value;
        };
        const saveDatos = () => {
            if (selectedUnidad.value == "") {
                selectedUnidad.value = placa.value
            }
            let contraseñaFinal = contraseña.value;

            if (contraseñaNueva.value !== "") {
                contraseñaFinal = md5(contraseñaNueva.value);
            }
            emit('aceptar-editar', {
                id_usuario: props.idUsuario,
                id_conductor: props.idConductor,
                apellidos: apellidos.value,
                dni: dni.value,
                placa: selectedUnidad.value,
                licencia: licencia.value,
                usuarios: usuarios.value,
                contraseña: contraseñaFinal
            })
        }
        onMounted(() => {
            buscarConductor(props.idUsuario, props.idConductor)
                .then(response => {
                    console.log(response.data.conductor)
                    apellidos.value = response.data.conductor.apellidos_nombres;
                    dni.value = response.data.conductor.numero_documento;
                    licencia.value = response.data.conductor.numero_licencia;
                    usuarios.value = response.data.conductor.usuario;
                    contraseña.value = response.data.conductor.clave;
                    placa.value = response.data.conductor.placa;
                    listarUnidadesLibres(store.state.codempresa)
                        .then(response => {
                            ListselectUnidades.value = transformDataCisternas(response.data.unidades, placa.value);
                        })
                })
        })
        return {
            selectedUnidad,
            ListselectUnidades,
            closeModal,
            handleUnidadSelected,
            saveDatos,
            saveApellidos,
            saveDni,
            saveLicencia,
            saveUsuario,
            updatepassword,
            apellidos,
            dni,
            licencia,
            usuarios,
            contraseña
        }
    },
};

function transformDataCisternas(data, placa) {
    const transformedData = data.map(item => ({ key: item.placa, name: item.placa }));
    if (placa === null || placa === '') {
        transformedData.unshift({ key: '', name: "Asignar unidad" });
    } else {
        transformedData.unshift({ key: placa, name: placa });
    }
    return transformedData;
}

</script>

