<template>
    <div class="fixed inset-0 flex items-center justify-center z-[9999]">
        <div class="absolute inset-0 bg-black opacity-30 pointer-events-none"></div>
        <!-- Container modal -->
        <div class="relative z-10 p-4 bg-white rounded-xl lg:min-w-[350px] lg:max-w-[450px] max-w-[340px] min-w-[330px]">
            <form class="p-2 text-gray-700 w-full h-full text-sm">
                <h3 class="text-center font-bold text-lg">Crear conductor</h3>
                <ItemForm @value-input="saveApellidos" label="Apellidos y nombres" name="apellidos_nombres" id="apellidos_nombres" placeholder="Inserte el nombre del conductor" value=""/>
                <ItemForm @value-input="saveDni" label="DNI" name="dni" id="dni" placeholder="Inserte el DNI del conductor" value=""/>
                <ItemForm @value-input="saveLicencia" label="Número de licencia" name="licencia" id="licencia" placeholder="Inserte el número de licencia"/>
                <SelectForm name="Asignar a una unidad" :options="ListselectUnidades"
                    v-model="selectedUnidad" @selected="handleUnidadSelected"
                    class="col-span-2 md:col-span-1 lg:col-span-2 text-sm my-2" />
                <ItemForm @value-input="saveUsuario" label="Usuario" name="usuario" id="usuario" placeholder="Inserte un usuario para el conductor"/>
                <PasswordField @update-paswoord="updatepassword" texto="Contraseña" placeholder="Ingrese una contraseña" />
                <PasswordField @update-paswoord="updatepasswordRepeat" texto="Confirmar contraseña" placeholder="Repita la contraseña"/>
                <p :class="{ 'text-green-500 text-xs': msgValidacion === 'Las contraseñas coinciden', 'text-red-400 text-xs': msgValidacion === 'Las contraseñas no coinciden' }"
                    v-if="msgValidacion !== ''">{{ msgValidacion }}</p>
            </form>
            <div class="btns flex gap-2 justify-center items-center pt-2">
                <button @click="closeModal"
                    class="bg-gray-400 hover:bg-gray-500 text-white text-sm p-2 px-8 rounded-xl transition duration-300">Cerrar</button>
                <button @click="saveDatos"
                    class="bg-blue-400 hover:bg-blue-500 text-white text-sm p-2 px-8 rounded-xl transition duration-300">Crear</button>
            </div>
        </div>
        <!-- Container modal -->
    </div>
</template>

<script>
import {ref, onMounted} from 'vue';
import ItemForm from '../generales/ItemForm.vue';
import {listarUnidadesLibres} from '@/services/UnidadesService';
import SelectForm from '../generales/SelectForm.vue';
import PasswordField from '../generales/PasswordField.vue';
import store from '@/store';

export default {
    name: 'ModalCreate',
    components: {ItemForm, PasswordField, SelectForm},
    emits: ['close-modal', 'save-datos'],
    setup(_, { emit }) {
        const apellidos = ref('');
        const dni = ref('');
        const licencia = ref('');
        const ListselectUnidades = ref([]);
        const usuarios = ref('');
        const contraseña = ref('');
        const msgValidacion = ref('');
        const selectedUnidad = ref('');
        const repeatContraseña = ref('');
        const closeModal = () =>{
            emit('close-modal')
        }
        const saveDatos = () => {
            emit('save-datos',{
                apellidos: apellidos.value,
                dni: dni.value,
                placa: selectedUnidad.value,
                licencia: licencia.value,
                usuarios: usuarios.value,
                contraseña: contraseña.value
            })
        }

        const saveApellidos = (value) =>{
            apellidos.value = value;
        }
        const saveDni = (value) =>{
            dni.value = value;
        }
        const saveLicencia = (value) => {
            licencia.value = value;
        }
        const saveUsuario = (value) =>{
            usuarios.value = value;
        }
        const updatepassword = (value) =>{
            contraseña.value = value;
        }
        const updatepasswordRepeat = (value) => {
            repeatContraseña.value = value;
            validatePassword();
        }
        const handleUnidadSelected = (value) => {
            selectedUnidad.value = value;
        };
        onMounted(() => {
            listarUnidadesLibres(store.state.codempresa)
                .then(response => {
                    ListselectUnidades.value = transformDataCisternas(response.data.unidades);
                })
        })
        const validatePassword = () => {
            if (contraseña.value === repeatContraseña.value) {
                msgValidacion.value = 'Las contraseñas coinciden';
            } else {
                msgValidacion.value = 'Las contraseñas no coinciden';
            }
        }
        return {
            closeModal,
            saveDatos,
            ListselectUnidades,
            saveApellidos,
            handleUnidadSelected,
            saveDni,
            saveLicencia,
            saveUsuario,
            updatepassword,
            msgValidacion,
            updatepasswordRepeat
        }
    },
};

function transformDataCisternas(data) {
    const transformedData = data.map(item => ({ key: item.placa, name: item.placa }));
    transformedData.unshift({ key: '', name: 'Eliga una cisterna' });
    return transformedData;
}
</script>

