<template>
    <table class="w-full text-xs text-left text-gray-800">
        <thead class="text-xs text-gray-700 uppercase bg-gray-100">
            <tr>
                <th scope="col" class="px-6 py-3">
                    Usuario
                </th>
                <th scope="col" class="px-6 py-3">
                    Codempresa
                </th>
                <th scope="col" class="px-6 py-3">
                    Placa
                </th>
                <th scope="col" class="px-6 py-3">
                    Nombre y apellidos
                </th>
                <th scope="col" class="px-6 py-3">
                    Número de licencia
                </th>
                <th scope="col" class="px-6 py-3">
                    Acciones
                </th>
            </tr>
        </thead>
        <tbody>
            <tr v-if="listConductores.length === 0">
                <td class="border px-4 py-4 text-xs text-center" colspan="6">No se encontró ningún dato
                    para esta tabla</td>
            </tr>
            <tr v-else v-for="conductor in listConductores" :key="conductor.id_usuario"
                class="bg-white border-b hover:bg-gray-50 ease-in duration-300">
                <td class="px-6 py-3">
                    {{ conductor.usuario }}
                </td>
                <td class="px-6 py-3">
                    {{ conductor.codempresa }}
                </td>
                <td class="px-6 py-3">
                    {{ conductor.placa }}
                </td>
                <th scope="row" class="px-6 py-3 font-medium text-gray-900 whitespace-nowrap">
                    {{ conductor.apellidos_nombres }}
                </th>
                <th scope="row" class="px-6 py-3 font-medium text-gray-900 whitespace-nowrap">
                    {{ conductor.numero_licencia }}
                </th>
                <td class="px-6 py-2 flex gap-2">
                    <button
                        class="font-medium py-[0.5rem] px-3 rounded-lg bg-green-500 hover:bg-green-600 ease-in duration-300 text-white"
                        @click.prevent="editarConductor(conductor.id_usuario, conductor.id_conductores, conductor.placa)">Editar</button>
                    <button
                        class="font-medium py-[0.5rem] px-3 rounded-lg bg-red-500 hover:bg-red-600 ease-in duration-300 text-white "
                        @click.prevent="eliminarConductor(conductor.id_usuario, conductor.id_conductores, conductor.placa)">Eliminar</button>
                </td>
            </tr>
        </tbody>
    </table>
</template>
<script>
export default ({
    name: "TableConductores",
    props: {
        listConductores: Array
    },
    methods: {
        eliminarConductor(id_usuario, id_conductores, placa) {
            this.$emit('eliminar-conductor', {
                id_usuario: id_usuario,
                id_conductores: id_conductores,
                placa: placa
            })
        },
        editarConductor(id_usuario, id_conductores) {
            this.$emit('editar-conductor', {
                id_usuario: id_usuario,
                id_conductores: id_conductores
            }
            )
        }
    }
})
</script>